.logo {
  max-width: 1000px;
  margin: 0 auto;
}

.logo path, .logo line {
  fill: none;
  stroke: #000;
  stroke-linecap: round;
  stroke-linejoin: round;
  stroke-miterlimit: 10;
  stroke-width: 11px;
}

.logo .logo-green-line {
  stroke: #0f2;
  stroke-width: 20px;
}

.logo .logo-dot {
  fill: #f0f8ff;
  animation: 2s ease-in-out infinite logo-dot;
}

@keyframes logo-dot {
  0% {
    opacity: 0;
    transform: translate(0, -17px);
  }

  10% {
    opacity: 1;
    transform: translate(0, -17px);
  }

  90% {
    opacity: 1;
    transform: translate(0, 155.9px);
  }

  100% {
    opacity: 0;
    transform: translate(0, 155.9px);
  }
}

.logo .logo-whisker {
  stroke-width: 6px;
}

.logo .logo-ncoder, .logo .logo-top-rat, .logo .logo-ecoder, .logo .logo-bottom-rat {
  opacity: 0;
  transition: all .2s .3s;
}

.logo .logo-e-r, .logo .logo-d-r {
  transition: all .3s .3s;
}

.logo .logo-e-r {
  transform: translate(-42%);
}

.logo .logo-d-r {
  transform: translate(-41.3%);
}

.logo:hover .logo-e-r, .logo:hover .logo-d-r {
  transition-delay: 0s;
  transform: translate(0%);
}

.logo:hover .logo-ncoder, .logo:hover .logo-top-rat, .logo:hover .logo-ecoder, .logo:hover .logo-bottom-rat {
  opacity: 1;
  transition: all .3s;
  transition-delay: 0;
}

